import react, {Component} from 'react';
import Layout from '../../components/layout'
import {Message} from 'bloomer'
import { Container, Title} from 'bloomer'
import web3 from '../../ethereum/web3'


export default class Dapps extends Component {
    state ={
        metamask: '',
        message: false,
        errorMessage: ''

    }
    checkMetamask(){
        console.log("check metamask")
        //this.setState({metamask: web3})
        if( web3 == ''){
            this.setState({errorMessage: 'please install metamast'})
            return  <Message error header="Oops!" content={this.state.errorMessage}/>
        }
    }
    render() {
        console.log("what is in ", web3)
        return ( <Layout>

            <Container>
            <Title isSize = {4}> The playground page </Title>
             <p> Ethereum playground </p>
             {this.checkMetamask()}
            
             
              </Container> </Layout>)
        }

    }


